.App {
  text-align: center;
  color:white;
  padding-top: 10vh;
  background-color: black;
}
body {
  background-color: black;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.card{
  background-image: url(./gold-trans.png);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: bottom;
  width: 50vh;
  max-width: 300px;
  max-height: 150px;
  height: 25vh;
  margin: auto;
  border-radius: 50px 50px 50px 50px;
  display: flex;
  flex-direction: column;
  margin-top: 5%;
  border: rgba(85, 74, 14, 0.795);
  border-width: 1px;
  border-style: solid;
  box-shadow: 0px 0px 50px 10px rgba(255, 217, 0, 0.315);
}
.card-coins {
max-width: 75%;
align-self: center;
font-size: 1.5em;
margin-right: 2%;
}

.card-head {
  width: 100%;
  
  height: 33%;
  border-radius: inherit;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
}

.card-logo {
width: auto;
height:50%;
align-self: center;
}

.card-middle {

height: 40%;
display: flex;
flex-direction: column;
}

.card-middle-text {
height: 40%;
font-size: 0.8em;
}

.card-middle-car {
height: 60%;
font-size: 1em;
}

.card-foot {

height: 25%;
font-size: 1.5em;
align-content: center;
}

.card-foot label {
  align-self: center;
}

.cars {
  padding: 0;
  padding-top: 0;
  margin-top: 0;
  width: 70%;
  max-width: 300px;
  margin: auto;
}
.cars label {
  font-size: 2em;
  display: block;
  margin-bottom: 1vh;
  background: -webkit-linear-gradient(#eee, rgb(153, 119, 8));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cars select {
  font-size: 1em;
  border-radius: 100px;
  border-style: solid;
  border-width: 1px;
  border-color: gold;
  background-color: rgb(255, 255, 255);
  height: 5vh;
  width: 100%;

  
}


.dayform {
width: 70vw;
max-width: 300px;
margin: auto;
transition: all 1s;
}

.dayform label {
  font-size: 2em;
  display: block;
  margin-bottom: 1vh;
  background: -webkit-linear-gradient(#eee, rgb(153, 119, 8));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.dayform input {
  font-size: 2em;
  border-radius: 100px;
  border-style: solid;
  border-width: 1px;

  border-color: gold;
  background-color:white;

  text-align: center;
  height: 5vh;
  width: 100%;
  margin-bottom: 1vh;
}

@keyframes example {
  from {transform: translateX(50px);
  opacity: 0;}
  to {transform: translateX(0px);
  opacity: 1;}
}


.car-container {
  width: 70vw;
  max-width: 300px;
  margin:auto;
  position: relative;
  display: block;
  height: 25vh;
  margin-bottom: 2vh;
  
}

.car-container img{
  width: 100%;
  max-height: inherit;
  max-width: inherit;
  animation-name: example;
  animation-duration: 1s;
}